.home{height:100vh
}
.home input{outline:none;border:none;border-radius:15px;padding:5px 15px
}
.home .model-frame{height:100vh;position:fixed;top:0;left:0;width:100%
}
.home .model-frame .turntable-frame{height:100%;width:100%
}
.home .model-frame .close-btn{width:25px;height:25px;border-radius:50%;top:1%;right:1%
}
.home .model-frame .go-btn{bottom:5%
}
.home .model-frame .turntable{position:absolute
}
.home .model-frame .turntable ul{background:gray;border:2px solid gray;width:300px;height:300px;border-radius:50%;overflow:hidden;position:absolute;margin:0
}
.home .model-frame .turntable li{height:150px;width:150px;position:absolute;-webkit-transform-origin:100% 100%;transform-origin:100% 100%;border:gray 1px solid
}
.home .model-frame .turntable li:nth-child(odd){background-color:#e6a7a7
}
.home .model-frame .turntable li:nth-child(1){-webkit-transform:rotate(0deg);transform:rotate(0deg)
}
.home .model-frame .turntable li:nth-child(even){background-color:#429489
}

.game{height:100vh
}
.game input{background:white;border:1px solid gray;border-radius:15px;padding:5px 10px;height:100%;color:gray;outline:none
}
.game input[type="checkbox"]{height:20px
}
.enter-btn{border:white 1px solid;padding:5px 15px;outline:none
}
.name-list{height:250px;overflow:scroll
}
.triangle{width:0;height:0;border-style:solid;border-width:12px 8.5px 0 8.5px;border-color:#ffffff transparent transparent transparent;margin-top:5px;-webkit-transition:0.5s;transition:0.5s;-webkit-transform:rotate(-180deg);transform:rotate(-180deg)
}
.slide-fade-enter-active{-webkit-transition:all .5s ease-in-out;transition:all .5s ease-in-out
}
.slide-fade-leave-active{-webkit-transition:all .5s ease-in-out;transition:all .5s ease-in-out
}
.slide-fade-enter,.slide-fade-leave-to{-webkit-transform:translateY(-10px);transform:translateY(-10px);opacity:0
}
